.detail {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;

  h6 {
    color: #000;
    font-size: .8rem !important;
    font-weight: 600;
    width: 100%;
  }

  p {
    color: #7e7e7e;
    width: 100%;
  }

  div[class^='col'] {
    padding-right: unset;
    padding-left: unset;
  }

  img {
    object-fit: cover;
    width: 100%;
    height: 167px;
  }

  div.col-12 {
    margin-bottom: 4px;
  }

  div.col-6:nth-child(2) {
    padding-right: 2px;
  }

  div.col-6:nth-child(3) {
    padding-left: 2px;
  }
}